import Layout from "./components/layout";
import fetch from "isomorphic-unfetch";

import Link from "next/link";
import { Grommet, Heading, Anchor, Text, Box, Layer, Image, Button } from "grommet";
import { Card } from "grommet-controls";
import { dark } from "grommet/themes";
import { Disposer } from "bluebird";
import { IconButton } from "grommet-controls/components";
import { SubtractCircle } from "grommet-icons";
import { isContinueStatement } from "@babel/types";

const Items = ({ items }) => {
  return (
    <Grommet theme={dark}>
      <Layout>
        <Box direction="row" wrap="true" alignContent="around" pad="medium">
          {items.map(item => (
            <Card
              basis="medium"
              animation="fadeIn"
              background="dark-1"
              elevation="xxsmall"
              gap="medium"
              margin="medium"
            >
              <Card.CardTitle color="brand">{item.name}</Card.CardTitle>
              <Card.CardContent color="dark-1">
                <Box
                  direction="row"
                  border={{ color: 'brand', size: 'large' }}
                  pad="medium"
                  round="small">
                  <Box height="small" width="medium">
                    <Image
                      fit="cover"
                      src={item.url} />
                  </Box></Box>
                <Text>{item.description}</Text>
                <Text>${item.price}</Text>
                <Text>{item.category}</Text>
              </Card.CardContent>
            </Card>
          ))}
        </Box>
      </Layout>
    </Grommet>
  );
};

Items.getInitialProps = async ({ req }) => {
  const res = await fetch("http://localhost:8080/api/items");
  // console.log("ressssss", res)
  const json = await res.json();
  return { items: json };
};

export default Items;
